html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: "Montserrat", sans-serif;
  color: $dark;
}

body {
  height: 100%;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  background: #F0F2FA;
}

html.overflowed,
html.overflowed body {
  overflow-y: hidden;
}

.cursor-pointer {
  cursor: pointer;
}

.text-dark-blue,
.text-dark-blue-hover:hover {
  color: #6382A8;
}

.text-white-hover:hover {
  color: $white;
}

.form-control::placeholder {
  color: #CED4DA;
}

//General style start

@for $i from 6 through 50 {
  @each $key, $value in (t: 'top', r: 'right', b: 'bottom', l: 'left') {
    .p#{$key}-#{$i * 2} {
      padding-#{$value}: #{$i * 2}px;
    }
  }
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

.focus-none {
  box-shadow: none !important;
  outline: none !important;
}

.has-bg {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.bg-pink,
.bg-pink-hover:hover {
  background-color: #F0F2FA !important;
}

.text-pink,
.text-pink-hover:hover {
  color: #F0F2FA !important;
}

.main-wrapper {
  display: flex;
}

table.table-separate {
  border-collapse: separate;
  border-spacing: 0 0;

  @for $i from 0 through 20 {

    &.space-#{$i} {
      border-spacing: #{$i}px;
    }

    &.spaceX-#{$i} {
      border-spacing: #{$i}px 0;
    }

    &.spaceY-#{$i} {
      border-spacing: 0 #{$i}px;
    }
  }
}

.row-bg-white tr {
  background-color: $white;
}

//General style end

//Sidebar start

.sidebar {
  min-width: 300px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  transition: all 0.7s ease-in-out;
  position: relative;

  &-fixed {
    width: 300px;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: #fff;
    position: fixed;
    left: 0;
    top: 0;
    transition: all 0.7s ease-in-out;
  }

  &.open {
    @include media(1140) {
      min-width: 65px;
    }

    .sidebar {
      &-fixed {
        @include media(1140) {
          width: 65px;
        }
      }

      &-link {
        &__text {
        }
      }

      &-avatar {
        height: 35px;
        width: 35px;

        img {
          height: 35px;
          width: 35px;
        }
      }

      &-support {
        padding: 40px 5px;
      }

      &-block {
        background: #4790FF;
        border-radius: 10px;
        display: flex;
        align-items: center;
        text-align: center;

        &_text {
          margin-left: 0;
        }
      }
    }

  }


  &-toggle {
    display: none;
    @include media(1140) {

      display: block;
    }
    position: absolute;
    top: 77px;
    right: -43px;
    background: #fff;
    padding: 5px 10px;
  }

  &-logo {
    display: inline-block;
  }

  &-link {
    display: flex;
    align-items: center;
    padding: 16px 22px;
    color: #5F5F5F;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    position: relative;

    &.active {
      background: #F0F2FA;
    }

    &__text {
      color: #5F5F5F;
      opacity: 0.6;
    }

    i {
      color: #989898;
    }
  }

  &-avatar {
    height: 48px;
    width: 48px;
    transition: all 0.7s ease-in-out;

    img {
      transition: all 0.7s ease-in-out;
      height: 48px;
      width: 48px;
    }
  }

  &-support {
    padding: 40px 20px;
    transition: all 0.7s ease-in-out;
  }

  &-block {
    transition: all 0.7s ease-in-out;
    background: #4790FF;
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 10px;

    &_text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      transition: all 0.7s ease-in-out;
      margin-left: 8px;
      height: 33px;


      p {
        transition: all 0.7s ease-in-out;
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        letter-spacing: 0.75px;
        color: #FFFFFF;
        margin-bottom: 0;
      }

      span {
        transition: all 0.7s ease-in-out;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        letter-spacing: 0.859533px;
        color: rgba(255, 255, 255, 0.9);
      }

    }
  }
}

//Sidebar end

//Login Page Start

.login {

  &-left {
    background: #FFFFFF;
    box-shadow: 0 4px 11px rgba(0, 0, 0, 0.35);
    position: relative;
    z-index: 2;
  }

  &-bg {
    background-image: url("../images/login-bg.png");
    min-height: 100vh;

    @include media(576) {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0.2;
      z-index: 1;
    }
  }

  &-forgot {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    text-decoration-line: underline;
    color: #192A3E;
  }

  &-support {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    color: #9A9A9A;
    margin-bottom: 5px;
  }

  &-phone,
  &-mail {
    display: block;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    color: #4790FF;
  }
}

//Login Page End

// Content Page Start

.content {
  margin-left: 26px;
  margin-right: 45px;
  margin-top: 20px;
  width: 100%;

  /* Navigation */
  &-navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;

    &_back {
      color: #C2C2C2;
      margin-right: 14px;
    }

    &_title {
      font-weight: 600;
      font-size: 18px;
      line-height: 21px;
      color: #6382A8;
      margin-bottom: 0;
    }

    .breadcrumb-item {
      color: #C2C2C2;
      font-size: 14px;
    }

    &_archive {
      background: #9F9F9F;
      border-radius: 4px;
      color: white;
      padding: 7px 32px;
      margin-right: 20px;
    }

    &_download {
      background: #4790FF;
      border-radius: 4px;
      padding: 7px 22px;
      margin-right: 20px;
    }

    &_add {
      background: #4790FF;
      border-radius: 4px;
      padding: 7px 20px;
      color: #FFF;
      margin-right: 20px;
    }

    &_filter {
      color: #4790FF;
      font-size: 20px;
      padding-left: 0;
      padding-right: 0;
    }
  }


  /* Table */
  &-table {

    &_thead {
      background: #4790FF;

      tr > th {
        border: none;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        color: #FFFFFF;
      }
    }

    &_tbody {
      background: #fff;

      tr:last-child th,
      tr:last-child td {
        border: none;
      }

      tr:last-child th {
        border-bottom-left-radius: 4.5px;
      }

      tr:last-child td:last-child {
        border-bottom-right-radius: 4.5px;
      }

      &__numbers {
        font-weight: 500;
        font-size: 16px;
        line-height: 21px;
        color: #556164;
      }

      &__text {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #9A9A9A;
      }

      &__icons {
        text-align: center;
        vertical-align: central;

        i {
          color: #969696;
        }
      }
    }
  }

  .table > :not(caption) > * > * {
    padding: 25px 20px;
    border-color: #e5e5e5;
  }

  &-frame:first-child,
  &-table:first-child {
    margin-top: 40px;
  }

  &-frame:last-child,
  &-table:last-child {
    margin-bottom: 0;
  }

  /* Frames */
  &-frame {
    background: #FFFFFF;
    border-radius: 4.5px 4.5px 4.5px 4.5px;
    padding: 15px 20px;
    margin-bottom: 10px;
    transition: all 0.3s ease-in-out;
    position: relative;
    z-index: 1;

    &.success {
      background: #B8F6CD;
    }

    &_hidden {
      height: 0;
      overflow: hidden;
      transition: height 0.2s ease-out;
    }

    &:hover {
      box-shadow: 0 4px 11px rgba(0, 133, 255, 0.25);
      z-index: 2;

      &.success {
        box-shadow: 0 4px 11px rgba(86, 238, 135, 0.25);
        z-index: 2
      }

      .content-frame_text {
        color: #0085FF;
      }
    }

    &_number {
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      color: #0085FF;

      span {
        font-size: 12px;
        color: #9A9A9A;
      }
    }

    &_name {
      font-weight: 500;
      font-size: 16px;
      line-height: 21px;
      color: #556164;
      margin-bottom: 20px;
    }

    &_title {
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      color: #3F3F3F;
      margin-right: 38px;
      padding-left: 0;
    }

    &_text {
      font-weight: 500;
      font-size: 12px;
      line-height: 21px;
      color: #556164;
      transition: all 0.3s ease-in-out;
      padding-right: 0;
      margin-bottom: 0;
    }

    &_status {
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      color: #3F3F3F;
      margin-right: 38px;
      padding-left: 0;
    }

    &_info {
      font-weight: 500;
      font-size: 12px;
      line-height: 21px;
      color: #556164;
      padding-right: 0;
      margin-bottom: 0;
    }

    &_toggle {
      position: absolute;
      right: 20px;
      bottom: 8px;
      font-size: 24px;
      color: #0085FF;
      transition: all 0.3s ease-in-out;

      .fal {
        transition: all 0.3s ease-in-out;
      }

      &.active {
        .fal {
          transform: rotate(45deg);
        }
      }
    }

    &_icons {
      color: #699BF7;
      margin-right: 22px;
      margin-top: 22px;
    }
  }

  /* Form */
  &-form {
    background: #FFFFFF;
    box-shadow: 0 4px 11px rgba(170, 170, 170, 0.25);
    padding: 20px 25px;
    margin-top: 40px;

    &_label {
      font-weight: 500;
      font-size: 12px;
      line-height: 21px;
      color: #8F8F8F;
    }

    &_input {

      &::placeholder {
        font-weight: 500;
        font-size: 12px;
        line-height: 21px;
        color: #CED4DA;
      }
    }

    &_submit {
      background: #4790FF;
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      text-align: center;
      color: #FFFFFF;
      padding: 7px 17px;
      margin-top: 15px;
    }
  }

  &-col {
    margin-bottom: 10px;
    &:nth-child(odd) {
      padding-right: 5px;
    }

    &:nth-child(even) {
      padding-left: 5px;
    }

    @include media(768) {
      &:nth-child(odd) {
        padding-right: 0;
      }

      &:nth-child(even) {
        padding-left: 0;
      }
    }
  }

  &-notification {
    background: #FFFFFF;
    box-shadow: 0 4px 11px rgba(143, 143, 143, 0.25);
    border-radius: 4.69118px;
    padding: 20px 30px;

    span {
      width: 10px;
      height: 10px;
      display: block;
      background: #F26464;
      border-radius: 50%;
      margin-right: 5px;
      margin-top: 8px;
    }

    &_title {
      font-weight: 600;
      font-size: 18px;
      line-height: 27px;
      letter-spacing: 0.01em;
      color: #1D1D1D;
      margin-bottom: 0;
    }

    &_date {
      font-weight: 300;
      font-size: 14px;
      line-height: 21px;
      color: #9A9A9A;
      margin-bottom: 0;
      margin-top: 8px;
    }

    &_text {
      font-weight: normal;
      font-size: 14px;
      line-height: 21px;
      color: #9A9A9A;
    }
  }

}

// Content Page End

// Pagination content Start
.grey-pagination .page-link {
  padding: 6px 10px;
  color: #CED4DA;
  border: none;
  font-weight: 300;
  font-size: 13px;
  line-height: 21px;
}

