}

// Pagination content End

// Dropdown content Start
.dropdown-menu {
  border: none;
  box-shadow: 0 4px 11px rgba(143, 143, 143, 0.25);
  border-radius: 3px;
}

.dropdown-menu::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #fff;
  position: absolute;
  z-index: 2;
  top: -10px;
  right: 0;
  margin-left: -2px;
  border-radius: 4px;
}

.dropdown-menu::after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  border-bottom: 9.5px solid #F0F2FA;
  position: absolute;
  z-index: 1;
  top: -11px;
  right: -1px;
  margin-left: -2px;
  border-radius: 4px;
}

.dropdown-item {
  font-weight: 500;
  font-size: 13px;
  line-height: 21px;
  color: #999A9D;
}

.dropdown-item.active, .dropdown-item:active {
  background: #F0F2FA;
  color: #999A9D;
}

// Dropdown content End
