/* Animation Classes */

.slide-in-fwd-left {
  -webkit-animation: slide-in-fwd-left 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  animation: slide-in-fwd-left 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

.slide-out-bck-left {
  -webkit-animation: slide-out-bck-left 0.4s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
  animation: slide-out-bck-left 0.4s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

.slide-out-bck-right {
  -webkit-animation: slide-out-bck-right 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
  animation: slide-out-bck-right 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

.slide-in-fwd-right {
  -webkit-animation: slide-in-fwd-right 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  animation: slide-in-fwd-right 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}


.slide-out-fwd-center {
  animation: slide-out-fwd-center 0.4s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

.slide-in-fwd-center {
  animation: slide-in-fwd-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@keyframes slide-in-fwd-center {
  0% {
    -webkit-transform: translateZ(-1400px);
    transform: translateZ(-1400px);
    opacity: 0;
    display: none;
    width: 0;
    height: 18px;
  }
  100% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1;
    display: block;
    width: auto;
    height: 18px;
  }
}

@keyframes slide-out-fwd-center {
  0% {
    -webkit-transform: translateZ(1);
    transform: translateZ(1);
    opacity: 1;
    display: block;
    width: auto;
    height: auto;
  }
  100% {
    -webkit-transform: translateZ(600px);
    transform: translateZ(600px);
    opacity: 0;
    display: none;
    width: 0;
    height: 18px;
  }
}

@keyframes slide-in-fwd-left {
  0% {
    -webkit-transform: translateZ(-1400px) translateX(-1000px);
    transform: translateZ(-1400px) translateX(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0) translateX(0);
    transform: translateZ(0) translateX(0);
    opacity: 1;
  }
}

@keyframes slide-out-bck-left {
  0% {
    -webkit-transform: translateZ(0) translateX(0);
    transform: translateZ(0) translateX(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateZ(-1100px) translateX(-1000px);
    transform: translateZ(-1100px) translateX(-1000px);
    opacity: 0;
  }
}


@keyframes slide-in-fwd-right {
  0% {
    -webkit-transform: translateZ(-1400px) translateX(1000px);
    transform: translateZ(-1400px) translateX(1000px);
    opacity: 0;
    width: auto;
  }
  100% {
    -webkit-transform: translateZ(0) translateX(0);
    transform: translateZ(0) translateX(0);
    opacity: 1;
    width: auto;
  }
}


@keyframes slide-out-bck-right {
  0% {
    -webkit-transform: translateZ(0) translateX(0);
    transform: translateZ(0) translateX(0);
    opacity: 1;
    width: auto;
  }
  100% {
    -webkit-transform: translateZ(-1100px) translateX(1000px);
    transform: translateZ(-1100px) translateX(1000px);
    opacity: 0;
    width: 0;
  }
}

@keyframes toggleHeight {
  0% {
    height: 0;
  }
  100% {
    height: auto;
  }
}